<template>
	<transition name="fade" mode="in-out">
		<div class='seeOceanPage' v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
			<v-Header class="head">
				<span slot='text'>看海洋</span>
			</v-Header>
			<nav>
				<ul :class='["menu",showMenu&&"show"]'>
          <li v-for='item in TagList' :class='item.tagId==tagId&&"active"' v-text='item.tagName' @click='changeTag(item.tagId)' :key='item.tagId'></li>
				</ul>
				<div class='showMenuBtn' @click="showMenuFun">
					<div :class="['btn',showMenu&&'show']">
						<span class="iconfont icon-arrow-top"></span>
					</div>
				</div>
			</nav>
			<div class="articleListsCon" v-for='articleInfo in articleLists' :key='articleInfo.id'>
				<v-article :articleInfo='articleInfo' />
			</div>
      <v-loadmove :type='loadType'/>

		</div>
	</transition>
</template>
<script>
	import Header from '../../components/header';
	import ArticleInfo from '../../components/seeOcean/articleInfo';
	import loadMove1 from '../../components/seeOcean/loadMore.vue';

	export default {
		name: '',
		components: {
			'v-Header': Header,
			'v-article': ArticleInfo,
			'v-loadmove': loadMove1
		},
		props: {},
		data() {
			return {
				tagId: 0,
        lastArticleId:0,
				btnList: [],
				showMenu: false,
				loading: false,
				articleLists: [],
        TagList:[],
        loadType:'more'
			}
		},
		watch: {
      tagId(newValue,old){
        this.articleLists = [];
        this.lastArticleId = 0;
        this.loading=false;
        this.loadType = 'more';
        this.getList();
      }
    },
		computed: {},
		created() {
      this.getTagList();
      this.getList();
		},
		methods: {
      loadMore(){
        this.getList();
      },
			showMenuFun() {
				this.showMenu = !this.showMenu;
			},
      changeTag(id){
        this.tagId = id;
      },
			getList() {
        if(this.loadType == 'all'||this.loading) return;
        this.loading = true;
        this.$http.get(this.$port.SEEOCEANLIST_PROT,{
          params:{
            tagId:this.tagId,
            lastArticleId:this.lastArticleId
          }
        }).then((response) => {
          const {data} = response;
          this.loading = false;
          if(data.code == 200){
            this.articleLists=this.articleLists.concat(data.data);
            if(data.data.length>0){
              this.lastArticleId = data.data.pop().articleId;
              this.loadType = 'more';
            }else{
              this.loadType = 'all';
            }
          }
        })
        .catch((error) => {
          this.loading = false;
          console.log(error.response.data);
        });
			},
      getTagList(){
        this.$http.get(this.$port.SEEOCEANTAGLIST_PROT,{
          params:{}
        }).then((response) => {
          const {data} = response;
          if(data.code == 200){
            this.TagList=[{tagId: 0, tagName: "全部"}].concat(data.data);
          }
        })
        .catch((error) => {
          console.log(error.response.data);
        });
      }
		}
	}
</script>
<style lang="scss" scoped>
	* {
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		-webkit-tap-highlight-color: transparent;
		border: none;
		margin: 0;
		padding: 0;
	}

	.seeOceanPage {
		background: white;
    width:7.5rem;
		position: absolute;
		top: 0;
		bottom: 0;
		margin: 0;
		overflow-x: hidden;
		overflow-y: scroll;
		-webkit-overflow-scrolling: touch;
	}

	nav {
		width: 7.5rem;
		overflow: hidden;
		height: auto;
		border-bottom: 0.01rem solid rgb(229, 229, 229);
		position: relative;
		.menu {
			width: 7.9rem;
			font-family: 'Microsoft YaHei';
			font-size: 0.3rem;
			height: 0.87rem;
			margin: 0 0 0 0.3rem;
			overflow: hidden;
			transition: height 0.4s;
			&.show {
				width: 6.9rem;
				margin: auto;
				height: 1.45rem;
			}
			li {
				float: left;
				margin: 0.3rem 0.5rem -0.3rem 0;
				color: #666666;
				&.active {
					color: #1797c6;
				}
			}
		}
		.showMenuBtn {
			width: 0.86rem;
			height: 0.56rem;
			position: absolute;
			top: 0.3rem;
			right: 0;
			background: rgba(255, 255, 255, 1);
			text-align: center;
			color: rgb(102, 102, 102);
			line-height: 0.3rem;
			.btn {
				align-items: center;
				justify-content: center;
				display: flex;
				width: 100%;
				height: 100%;
				transform: rotate(180deg);
				transition: transform 0.5s;
				&.show {
					transform: rotate(0deg);
				}
				.iconfont {
					font-size: 0.28rem;
				}
			}
		}
	}

	.articleListsCon {
		font-family: 'Microsoft YaHei';
		width: 100%;
		height: auto;
		margin: 0 auto;
		border-bottom: 0.01rem solid rgb(229, 229, 229);
	}
</style>



